import React, { useState, useEffect } from 'react';
import { ChevronDown, Edit, X, AlertTriangle } from 'lucide-react';
import axiosInstance from '@/api/axios/axiosInstance';

const FacebookConnectedPages = ({ isConnected, onDisconnect }) => {
  const [selectedPage, setSelectedPage] = useState('');
  const [pages, setPages] = useState([]);
  const [forms, setForms] = useState([]);
  const [loading, setLoading] = useState(false);
  const [showFieldMapping, setShowFieldMapping] = useState(false);
  const [selectedForm, setSelectedForm] = useState(null);
  const [fieldMappings, setFieldMappings] = useState({});

  // CRM Fields available for mapping
  const crmFields = [
    { value: 'email', label: 'Email' },
    { value: 'full_name', label: 'Full name' },
    { value: 'first_name', label: 'First name' },
    { value: 'last_name', label: 'Last name' },
    { value: 'phone', label: 'Phone' },
    { value: 'company', label: 'Company' },
    { value: 'job_title', label: 'Job Title' },
    { value: 'address', label: 'Address' },
    { value: 'city', label: 'City' },
    { value: 'state', label: 'State' },
    { value: 'zip_code', label: 'Zip Code' },
    { value: 'country', label: 'Country' }
  ];

  useEffect(() => {
    if (isConnected) {
      fetchPages();
    }
  }, [isConnected]);

  useEffect(() => {
    if (selectedPage) {
      fetchForms(selectedPage);
    }
  }, [selectedPage]);

  const fetchPages = async () => {
    try {
      setLoading(true);
      const response = await axiosInstance.get('/api/facebook/pages/stored');

      if (response.data.success) {
        setPages(response.data.data || []);
        if (response.data.data && response.data.data.length > 0) {
          setSelectedPage(response.data.data[0].id);
        }
      }
    } catch (error) {
      console.error('Error fetching pages:', error);
    } finally {
      setLoading(false);
    }
  };

  const fetchForms = async (pageId) => {
    try {
      setLoading(true);
      const response = await axiosInstance.get(`/api/facebook/forms/stored/${pageId}`);

      if (response.data.success) {
        setForms(response.data.data || []);
      }
    } catch (error) {
      console.error('Error fetching forms:', error);
    } finally {
      setLoading(false);
    }
  };

  const handleEditFields = (form) => {
    setSelectedForm(form);
    // Load existing field mappings for this form
    setFieldMappings(form.field_mapping || {});
    setShowFieldMapping(true);
  };

  const handleSaveFieldMapping = async () => {
    try {
      const response = await axiosInstance.put(`/api/facebook/forms/${selectedForm.id}/field-mapping`, {
        field_mapping: fieldMappings
      });

      if (response.data.success) {
        // Update the form in the local state
        setForms(forms.map(form =>
          form.id === selectedForm.id
            ? { ...form, field_mapping: fieldMappings }
            : form
        ));
        setShowFieldMapping(false);
        setSelectedForm(null);
      }
    } catch (error) {
      console.error('Error saving field mapping:', error);
    }
  };

  const toggleFormStatus = async (formId, currentStatus) => {
    try {
      const newStatus = !currentStatus;
      const response = await axiosInstance.put(`/api/facebook/forms/${formId}/toggle`, {
        is_enabled: newStatus
      });

      if (response.data.success) {
        setForms(forms.map(form =>
          form.id === formId
            ? { ...form, is_enabled: newStatus }
            : form
        ));
      }
    } catch (error) {
      console.error('Error toggling form status:', error);
    }
  };

  if (!isConnected) {
    return null;
  }

  return (
    <div className=" rounded-lg border neo-card border-none \ p-6">
      <div className="flex items-center  justify-between mb-6">
        <h2 className="text-xl font-semibold text-white">Facebook Form Fields Mapping</h2>
        <button
          onClick={onDisconnect}
          className="px-4 py-2 rounded-lg text-sm font-medium
          bg-red-500/20 text-red-400
          border border-red-500/30
          hover:bg-red-500/30 hover:text-red-300
          transition"
        >
          Disconnect
        </button>
      </div>

      {/* Page Selection */}
      <div className="mb-6">
        <label className="block text-sm font-medium text-gray-500 mb-2">
          Select Page
        </label>
        <div className="relative">
          <select
            value={selectedPage}
            onChange={(e) => setSelectedPage(e.target.value)}
            className="
    w-full px-3 py-2 pr-10 rounded-lg
    bg-white/5 text-white
    border border-white/20
    focus:outline-none
    focus:ring-2 focus:ring-indigo-500/50
    focus:border-indigo-500/50
    appearance-none
  "
          >
            <option value="" className="bg-[#0b0f2e] text-white">Select a page...</option>
            {pages.map((page) => (
              <option key={page.id} value={page.id} className="bg-[#0b0f2e] text-white">
                {page.name}
              </option> 
            ))}
          </select>
          <ChevronDown className="absolute right-3 top-1/2 -translate-y-1/2 h-4 w-4 text-white/40 pointer-events-none"  />
        </div>
      </div>

      {/* Forms Table */}
      {selectedPage && (
        <div className="overflow-hidden">
          <div className="grid grid-cols-4 gap-4 px-4 py-3
  bg-white/5
  border-b border-white/10
  rounded-lg
  text-sm font-medium text-white/70">
            <div>Page Name</div>
            <div>Form Name</div>
            <div>Map Fields</div>
            <div>Status</div>
          </div>

          {loading ? (
            <div className="py-8 text-center text-gray-500">
              Loading forms...
            </div>
          ) : forms.length === 0 ? (
            <div className="py-8 text-center text-gray-500">
              No forms found for this page
            </div>
          ) : (
            forms.map((form) => {
              const currentPage = pages.find(p => p.id === selectedPage);
              return (
                <div key={form.id} className="
                grid grid-cols-4 gap-4 px-4 py-4 items-center
                border-b border-white/5
                text-white/80
                hover:bg-white/5
                transition
              ">
                  <div className="text-sm text-white">
                    {currentPage?.name || 'Unknown Page'}
                  </div>
                  <div className="text-sm text-white">
                    {form.form_name || form.name}
                  </div>
                  <div>
                    <button
                      onClick={() => handleEditFields(form)}
                      className="
    inline-flex items-center gap-1
    px-3 py-1.5 text-sm
    rounded-md
    bg-indigo-500/20 text-indigo-300
    border border-indigo-500/30
    hover:bg-indigo-500/30
    transition"
                    >
                      <Edit className="h-3 w-3 mr-1" />
                      Edit Fields
                    </button>
                  </div>
                  <div>
                    <label className="relative inline-flex items-center cursor-pointer">
                      <input
                        type="checkbox"
                        checked={form.is_enabled || false}
                        onChange={() => toggleFormStatus(form.id, form.is_enabled)}
                        className="sr-only peer"
                      />
                      <div className="
  w-11 h-6 rounded-full
  bg-white/10
  peer-checked:bg-indigo-500/70
  transition
  relative
"></div>
                    </label>
                  </div>
                </div>
              );
            })
          )}
        </div>
      )}

      {/* Field Mapping Modal */}
      {showFieldMapping && selectedForm && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
          <div className="bg-white rounded-lg shadow-xl max-w-2xl w-full mx-4 max-h-[90vh] overflow-y-auto">
            <div className="flex items-center justify-between p-6 border-b border-gray-200">
              <h3 className="text-lg font-semibold text-gray-900">Map Fields</h3>
              <button
                onClick={() => setShowFieldMapping(false)}
                className="text-gray-400 hover:text-gray-600"
              >
                <X className="h-5 w-5" />
              </button>
            </div>

            <div className="p-6">
              <p className="text-sm text-gray-600 mb-6">
                Map the fields in your form to the CRM fields. If you have any questions, we recommend checking out the{' '}
                <a href="#" className="text-blue-600 hover:underline">support article by clicking here</a>.
              </p>

              <div className="grid grid-cols-2 gap-6 mb-6">
                <div>
                  <h4 className="text-sm font-medium text-gray-700 mb-3">Form Fields</h4>
                </div>
                <div>
                  <h4 className="text-sm font-medium text-gray-700 mb-3">CRM Fields</h4>
                </div>
              </div>

              {/* Form Fields Mapping */}
              {selectedForm.questions && selectedForm.questions.length > 0 ? (
                selectedForm.questions.map((question, index) => (
                  <div key={index} className="grid grid-cols-2 gap-6 mb-4">
                    <div className="flex items-center">
                      <label className="text-sm text-gray-900">
                        {question.label}
                        {question.type === 'EMAIL' && <span className="text-red-500 ml-1">*</span>}
                      </label>
                    </div>
                    <div>
                      <select
                        value={fieldMappings[question.key] || ''}
                        onChange={(e) => setFieldMappings({
                          ...fieldMappings,
                          [question.key]: e.target.value
                        })}
                        className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                      >
                        <option value="">Select CRM field...</option>
                        {crmFields.map((field) => (
                          <option key={field.value} value={field.value}>
                            {field.label}
                          </option>
                        ))}
                      </select>
                    </div>
                  </div>
                ))
              ) : (
                <div className="text-center py-8 text-gray-500">
                  No form fields available for mapping
                </div>
              )}

              <div className="flex items-center p-4 bg-orange-50 border border-orange-200 rounded-lg mb-6">
                <AlertTriangle className="h-5 w-5 text-orange-500 mr-3 flex-shrink-0" />
                <p className="text-sm text-orange-700">
                  Map any of the above mandatory fields to receive leads into your account successfully.
                </p>
              </div>

              <div className="flex justify-end space-x-3">
                <button
                  onClick={() => setShowFieldMapping(false)}
                  className="px-4 py-2 text-gray-700 bg-gray-100 rounded-lg hover:bg-gray-200 transition-colors"
                >
                  Cancel
                </button>
                <button
                  onClick={handleSaveFieldMapping}
                  className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors"
                >
                  Confirm
                </button>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default FacebookConnectedPages;
