import React, { useState, useEffect } from "react";
import { toast } from "sonner";
import axiosInstance from "@/api/axios/axiosInstance";
import { Card, CardContent } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import whatsappImg from "../../../assets/whatsapp.png";
import WhatsappConnectModal from "../components/WhatsappConnectModal";

const Whatsapp = () => {
  const [isConnected, setIsConnected] = useState(false);
  const [profile, setProfile] = useState(null);
  const [isLoading, setIsLoading] = useState(true);
  const [isModalOpen, setIsModalOpen] = useState(false);

  const checkConnection = async () => {
    try {
      setIsLoading(true);
      const response = await axiosInstance.get("/api/whatsapp/auth/status");
      if (response.data.success && response.data.data.isConnected) {
        setIsConnected(true);
        setProfile(response.data.data.profile);
      } else {
        setIsConnected(false);
        setProfile(null);
      }
    } catch (error) {
      console.error("Error checking WhatsApp connection:", error);
    } finally {
      setIsLoading(false);
    }
  };

  useEffect(() => {
    checkConnection();
  }, []);

  const handleConnect = () => {
    // The modal now handles the connection via OTP verification
    // This function just needs to refresh the status
    toast.success("WhatsApp status refreshed");
    checkConnection();
  };

  const handleDisconnect = async () => {
    try {
      const response = await axiosInstance.delete("/api/whatsapp/auth/disconnect");
      if (response.data.success) {
        setIsConnected(false);
        setProfile(null);
        toast.success("WhatsApp disconnected successfully");
      }
    } catch (error) {
      console.error("Error disconnecting WhatsApp:", error);
      toast.error("Failed to disconnect WhatsApp");
    }
  };

  return (
    <>
      <Card className="w-full max-w-sm bg-white/5 backdrop-blur-md border border-white/10 rounded-lg shadow-sm hover:shadow-md hover:border-indigo-500/30 transition-all duration-200">
        <CardContent className="flex flex-col items-center text-center p-2 space-y-6">
          <div className="w-20 h-20 rounded-full overflow-hidden flex items-center justify-center bg-white/10 border-2 border-white/20">
            <img src={whatsappImg} alt="WhatsApp" className="w-full h-full object-cover" />
          </div>
          <p className="text-gray-300 text-sm leading-relaxed">
            {isConnected && profile?.email ? `Connected: ${profile.email}` : "Connect your WhatsApp Account"}
          </p>
          <Button
            onClick={isConnected ? handleDisconnect : () => setIsModalOpen(true)}
            disabled={isLoading}
            className={`w-full py-3 rounded-md font-medium transition-colors duration-200 ${
              isConnected
                ? "bg-gradient-to-r from-red-500 via-red-500 to-red-500 text-white border-0"
                : "bg-gradient-to-r from-indigo-600 to-purple-600 hover:from-indigo-500 hover:to-purple-500 text-white border-0"
            }`}
          >
            {isLoading ? "Checking..." : isConnected ? "Disconnect" : "Connect"}
          </Button>
        </CardContent>
      </Card>

      <WhatsappConnectModal
        isOpen={isModalOpen}
        onClose={() => setIsModalOpen(false)}
        onConnect={handleConnect}
      />
    </>
  );
};

export default Whatsapp;
