import React, { useState } from "react";
import Page from "@/components/Page";
import Header from "@/components/Header";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import {
  Settings,
  AlertTriangle,
  CheckCircle,
  XCircle,
  RefreshCw,
  ExternalLink,
  Users,
  FileText,
  Zap
} from "lucide-react";
import { toast } from "sonner";
import axiosInstance from "@/api/axios/axiosInstance";

import linkedInImg from "../../assets/linkedin.png"
import gmailImg from "../../assets/gmail.png"

import FacebookConnectedPages from './components/FacebookConnectedPages';
import GmailOAuthModal from '@/components/GmailOAuthModal';
import LinkedIn from './LinkedIn/LinkedIn';
import Whatsapp from './Whatsapp/Whatsapp';



const Integrations = () => {
  const [facebookConnected, setFacebookConnected] = useState(false);
  const [gmailConnected, setGmailConnected] = useState(false);
  const [gmailProfile, setGmailProfile] = useState(null);
  const [activeTab, setActiveTab] = useState("integrations");
  const [showGmailModal, setShowGmailModal] = useState(false);

  // Check Gmail connection status (using separate Integration Gmail API)
  const checkGmailConnection = async () => {
    try {
      console.log('ðŸ” Checking Integration Gmail connection status...');
      const response = await axiosInstance.get('/api/integration-gmail/auth/status');

      if (response.data.success && response.data.data.isConnected) {
        console.log('âœ… Integration Gmail is connected:', response.data.data.profile);
        setGmailConnected(true);
        setGmailProfile(response.data.data.profile);
      } else {
        console.log('âŒ Integration Gmail is not connected');
        setGmailConnected(false);
        setGmailProfile(null);
      }
    } catch (error) {
      console.error('âŒ Error checking Integration Gmail connection:', error);
      setGmailConnected(false);
      setGmailProfile(null);
    }
  };

  const checkFacebookConnection = async () => {
    try {
      console.log('ðŸ” Checking Facebook connection status...');

      const response = await axiosInstance.get('/api/facebook/connection-status');

      console.log('ðŸ“Š Facebook connection status response:', response.data);
      console.log('ðŸ“Š Facebook connection data details:', JSON.stringify(response.data.data, null, 2));

      if (response.data.success && response.data.data.connected) {
        console.log('âœ… Facebook is connected, updating state to true');
        setFacebookConnected(true);
      } else {
        console.log('âŒ Facebook is not connected, updating state to false');
        console.log('ðŸ“Š credentials_count:', response.data.data?.credentials_count);
        console.log('ðŸ“Š connected_pages_count:', response.data.data?.connected_pages_count);
        setFacebookConnected(false);
      }
    } catch (error) {
      console.error('âŒ Error checking Facebook connection:', error);
      // Don't change state on error, just log it
    }
  };

  // Check Facebook and Gmail connection status on component mount only
  React.useEffect(() => {
    checkFacebookConnection();
    checkGmailConnection();
    // Clear any stale localStorage results on mount
    localStorage.removeItem('facebook_oauth_result');
    localStorage.removeItem('gmail_oauth_result');
  }, []);

  // Handler for processing Facebook OAuth result
  const handleFacebookOAuthResult = React.useCallback((data) => {
    if (data.type === 'facebook_oauth_success') {
      console.log('ðŸŽ‰ Facebook auth success received!', data);
      setFacebookConnected(true);
      console.log('âœ… setFacebookConnected(true) called - button should change now!');
      toast.success('Facebook connected successfully!');
      setActiveTab('facebook-pages');
      console.log('âœ… Switched to facebook-pages tab');
    }
  }, []);

  // Handler for processing Gmail OAuth result
  const handleGmailOAuthResult = React.useCallback((data) => {
    if (data.type === 'GMAIL_AUTH_SUCCESS') {
      console.log('ðŸŽ‰ Gmail auth success received!', data);
      setGmailConnected(true);
      if (data.data) {
        setGmailProfile({ email: data.data.email });
      }
      console.log('âœ… setGmailConnected(true) called - button should change now!');
      toast.success(`Gmail connected successfully! (${data.data?.email || 'Connected'})`);
      setShowGmailModal(false);
    } else if (data.type === 'GMAIL_AUTH_ERROR') {
      console.log('âŒ Gmail auth error received:', data);
      toast.error(data.message || 'Failed to connect Gmail');
      setShowGmailModal(false);
    }
  }, []);

  // Listen for OAuth messages via multiple methods
  React.useEffect(() => {
    // Method 1: Listen for postMessage
    const handleMessage = (event) => {
      console.log('ðŸ”” Message received:', event.data, 'from origin:', event.origin);

      if (!event.data || typeof event.data !== 'object') return;

      if (event.data.type === 'facebook_oauth_success') {
        handleFacebookOAuthResult(event.data);
      } else if (event.data.type === 'GMAIL_AUTH_SUCCESS' || event.data.type === 'GMAIL_AUTH_ERROR') {
        handleGmailOAuthResult(event.data);
      }
    };

    // Method 2: Listen via BroadcastChannel for Facebook
    let facebookChannel = null;
    try {
      facebookChannel = new BroadcastChannel('facebook_oauth_channel');
      facebookChannel.onmessage = (event) => {
        console.log('ðŸ” Received Facebook BroadcastChannel message:', event.data);
        if (event.data && typeof event.data === 'object') {
          handleFacebookOAuthResult(event.data);
        }
      };
    } catch (e) {
      console.log('âš ï¸ Facebook BroadcastChannel not supported');
    }

    // Method 3: Poll localStorage for Facebook results
    const checkLocalStorage = () => {
      try {
        const fbResult = localStorage.getItem('facebook_oauth_result');
        if (fbResult) {
          const data = JSON.parse(fbResult);
          if (data.timestamp && Date.now() - data.timestamp < 30000) {
            console.log('ðŸ” Found Facebook localStorage result:', data);
            localStorage.removeItem('facebook_oauth_result');
            handleFacebookOAuthResult(data);
          }
        }
      } catch (e) {
        // Ignore parsing errors
      }
    };

    const storageInterval = setInterval(checkLocalStorage, 500);

    window.addEventListener('message', handleMessage);
    console.log('âœ… Message listeners added for OAuth');

    return () => {
      window.removeEventListener('message', handleMessage);
      if (facebookChannel) {
        facebookChannel.close();
      }
      clearInterval(storageInterval);
      console.log('ðŸ§¹ Message listeners removed');
    };
  }, [handleFacebookOAuthResult, handleGmailOAuthResult]);

  const handleFacebookDisconnect = async () => {
    try {
      const response = await axiosInstance.delete('/api/facebook/disconnect');

      if (response.data.success) {
        setFacebookConnected(false);
        setActiveTab('integrations');
        toast.success('Facebook disconnected successfully');
        console.log('Facebook disconnected successfully');
      }
    } catch (error) {
      console.error('Error disconnecting Facebook:', error);
      toast.error('Failed to disconnect Facebook');
    }
  };


  const handleFacebookConnect = async () => {
    try {
      console.log('ðŸ” Debug - Initiating Facebook OAuth...');

      // Get OAuth URL from backend using axiosInstance (handles auth automatically)
      const response = await axiosInstance.get('/api/auth/facebook/oauth-url');

      console.log('ðŸ” Debug - Response data:', response.data);

      if (response.data.success && response.data.data.authUrl) {
        console.log('ðŸ” Debug - Opening OAuth URL:', response.data.data.authUrl);
        window.open(response.data.data.authUrl, 'facebook-auth', 'width=600,height=600');
      } else {
        throw new Error(`Invalid OAuth URL response: ${response.data.message || 'No auth URL received'}`);
      }
    } catch (error) {
      console.error('âŒ Error initiating Facebook OAuth:', error);

      // Handle authentication errors
      if (error.response?.status === 401 || error.response?.status === 403) {
        alert('Your session has expired. Please log in again.');
        window.location.href = '/#/login';
        return;
      }

      alert(`Failed to start Facebook authentication: ${error.message}`);
    }
  };



  // Gmail connect - open modal
  const handleGmailConnect = () => {
    console.log("ðŸ”„ Opening Gmail OAuth modal...");
    setShowGmailModal(true);
  };

  // Gmail disconnect (using separate Integration Gmail API)
  const handleGmailDisconnect = async () => {
    try {
      console.log('ðŸ”„ Disconnecting Integration Gmail...');
      const response = await axiosInstance.delete('/api/integration-gmail/auth/disconnect');

      if (response.data.success) {
        setGmailConnected(false);
        setGmailProfile(null);
        toast.success('Gmail disconnected successfully');
        console.log('âœ… Integration Gmail disconnected successfully');
      } else {
        toast.error(response.data.message || 'Failed to disconnect Gmail');
      }
    } catch (error) {
      console.error('âŒ Error disconnecting Integration Gmail:', error);
      toast.error('Failed to disconnect Gmail');
    }
  };

  // Gmail OAuth success callback
  const handleGmailSuccess = (data) => {
    console.log('ðŸŽ‰ Gmail OAuth success callback:', data);
    setGmailConnected(true);
    if (data?.email) {
      setGmailProfile({ email: data.email });
    }
    setShowGmailModal(false);
    // Re-check connection status from backend
    setTimeout(() => {
      checkGmailConnection();
    }, 1000);
  };

  const integrations = [
    {
      description: "Connect your location's Facebook Account",
      image: "https://upload.wikimedia.org/wikipedia/commons/5/51/Facebook_f_logo_%282019%29.svg",
      isConnected: facebookConnected,
      onConnect: handleFacebookConnect,
      onManage: handleFacebookDisconnect
    },
    {
      description: gmailConnected && gmailProfile?.email
        ? `Connected: ${gmailProfile.email}`
        : "Connect your location's Gmail Account",
      image: gmailImg,
      isConnected: gmailConnected,
      onConnect: handleGmailConnect,
      onManage: handleGmailDisconnect
    },
    
  ];

  return (
    <Page className="app-shell-bg relative overflow-hidden">
      {/* Neon glow layer */}
      <div aria-hidden className="absolute inset-0 pointer-events-none bg-gradient-to-r from-indigo-500/10 via-purple-500/10 to-pink-500/10" />

      <div className="relative z-10">
        <h4 className="text-2xl font-bold text-white mb-4">Integrations</h4>

        <div className="space-y-6">
          <Tabs value={activeTab} onValueChange={setActiveTab} className="w-full bg-transparent text-white">
            <TabsList className="flex w-auto bg-transparent p-8">
              <TabsTrigger value="integrations" className="flag-tab ">Integrations</TabsTrigger>
              <TabsTrigger value="facebook-pages" className="flag-tab">Facebook Connected Pages</TabsTrigger>
            </TabsList>

            <TabsContent value="integrations" className="space-y-10">
              <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 ">
                {integrations.map((integration, index) => (
                  <IntegrationCard
                    key={index}
                    {...integration}
                  />
                ))}
                <LinkedIn />
                <Whatsapp />
              </div>

            </TabsContent>

            <TabsContent value="facebook-pages" className="space-y-6">
              <FacebookConnectedPages
                isConnected={facebookConnected}
                onDisconnect={handleFacebookDisconnect}
              />
            </TabsContent>
          </Tabs>
        </div>

        {/* Gmail OAuth Modal - uses separate Integration Gmail API */}
        <GmailOAuthModal
          isOpen={showGmailModal}
          onClose={() => setShowGmailModal(false)}
          onSuccess={handleGmailSuccess}
          redirectPath="/integrations"
          useIntegrationApi={true}
        />


      </div>
    </Page>
  );
};




const IntegrationCard = ({
  description,
  image,
  isConnected,
  onConnect,
  onManage
}) => {
  return (
    <Card className="w-full max-w-sm bg-white/5 backdrop-blur-md border border-white/10 rounded-lg shadow-sm hover:shadow-md hover:border-indigo-500/30 transition-all duration-200">
      <CardContent className="flex flex-col items-center text-center p-2 space-y-6">
        {/* Circular Logo Image */}
        <div className="w-20 h-20 rounded-full overflow-hidden flex items-center justify-center bg-white/10 border-2 border-white/20">
          <img
            src={image}
            alt="Integration logo"
            className="w-full h-full object-cover"
          />
        </div>

        {/* Description */}
        <p className="text-gray-300 text-sm leading-relaxed">
          {description}
        </p>

        {/* Action Button */}
        <Button
          onClick={isConnected ? onManage : onConnect}
          className={`w-full py-3 rounded-md font-medium transition-colors duration-200 ${
            isConnected
              ? `bg-gradient-to-r 
        from-red-500
        via-red-500 
        to-red-500 text-white border-0`
              : 'bg-gradient-to-r from-indigo-600 to-purple-600 hover:from-indigo-500 hover:to-purple-500 text-white border-0'
          }`}
        >
          {isConnected ? 'Disconnect' : 'Connect'}
        </Button>
      </CardContent>
    </Card>
  );
};

export default Integrations;
