import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogDescription, DialogFooter } from "@/components/ui/dialog";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { CheckCircle2, MessageSquare, AlertCircle, ShieldCheck } from "lucide-react";
import axiosInstance from "@/api/axios/axiosInstance";
import { toast } from "sonner";
import React, { useState } from 'react';

const WhatsappConnectModal = ({ isOpen, onClose, onConnect }) => {
  const [provider, setProvider] = useState('meta');
  const [isLoading, setIsLoading] = useState(false);
  const [isVerifying, setIsVerifying] = useState(false);
  const [step, setStep] = useState('credentials'); // 'credentials' or 'otp'
  const [otpCode, setOtpCode] = useState('');

  // Meta States
  const [metaCreds, setMetaCreds] = useState({
    phoneNumberId: '',
    accessToken: '',
    wabaId: '',
    displayPhoneNumber: ''
  });

  // Twilio States
  const [twilioCreds, setTwilioCreds] = useState({
    accountSid: '',
    authToken: '',
    whatsappNumber: ''
  });

  const handleVerify = async () => {
    setIsVerifying(true);
    try {
      const credentials = provider === 'meta' ? metaCreds : twilioCreds;
      const response = await axiosInstance.post("/api/whatsapp/auth/send-otp", {
        provider,
        credentials
      });
      
      if (response.data.success) {
        setStep('otp');
        toast.success(`OTP sent to your phone via ${provider === 'meta' ? 'WhatsApp' : 'SMS'}!`);
      } else {
        toast.error(response.data.message || "Failed to send OTP");
      }
    } catch (error) {
      console.error("OTP send error:", error);
      toast.error(error.response?.data?.message || "Failed to send OTP. Please check credentials.");
    } finally {
      setIsVerifying(false);
    }
  };

  const handleConfirmOTP = async () => {
    setIsLoading(true);
    try {
      const response = await axiosInstance.post("/api/whatsapp/auth/verify-otp", {
        code: otpCode
      });
      
      if (response.data.success) {
        toast.success("WhatsApp connected successfully!");
        onConnect(); // Refresh status in parent
        onClose();
      } else {
        toast.error(response.data.message || "Invalid OTP");
      }
    } catch (error) {
      console.error("OTP verify error:", error);
      toast.error(error.response?.data?.message || "Verification failed");
    } finally {
      setIsLoading(false);
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (step === 'otp') {
      handleConfirmOTP();
    } else {
      handleVerify();
    }
  };

  const isFormFilled = () => {
    if (provider === 'meta') {
      return metaCreds.phoneNumberId && metaCreds.accessToken && metaCreds.wabaId && metaCreds.displayPhoneNumber;
    }
    return twilioCreds.accountSid && twilioCreds.authToken && twilioCreds.whatsappNumber;
  };

  return (
    <Dialog open={isOpen} onOpenChange={onClose}>
      <DialogContent className="sm:max-w-[1000px] bg-[#0b0f2e] text-white border-white/10 p-0 overflow-hidden">
        <div className="p-6 pb-0">
          <DialogHeader>
            <DialogTitle className="flex items-center gap-2 text-xl">
              <MessageSquare className="w-6 h-6 text-green-500" />
              Connect WhatsApp Business
            </DialogTitle>
            <DialogDescription className="text-gray-400 mt-2">
              {step === 'otp' 
                ? `Enter the verification code sent to your phone via ${provider === 'meta' ? 'WhatsApp' : 'SMS'}.`
                : "Choose your provider and enter credentials to enable automated messaging."}
            </DialogDescription>
          </DialogHeader>
        </div>

        <form onSubmit={handleSubmit}>
          {step === 'credentials' ? (
            <Tabs defaultValue="meta" className="w-full mt-4" onValueChange={(val) => setProvider(val)}>
              <TabsList className="grid w-full grid-cols-2 bg-white/5 mx-6 w-auto border border-white/10">
                <TabsTrigger value="meta" className="data-[state=active]:bg-green-600/20 data-[state=active]:text-green-400">
                  Meta Cloud API
                </TabsTrigger>
                <TabsTrigger value="twilio" className="data-[state=active]:bg-blue-600/20 data-[state=active]:text-blue-400">
                  Twilio API
                </TabsTrigger>
              </TabsList>

              <div className="p-6 pt-4">
                <TabsContent value="meta" className="space-y-4 mt-0">
                  <div className="space-y-2">
                    <Label className="text-gray-300">Phone Number ID</Label>
                    <Input
                      placeholder="e.g. 106312345678901"
                      autoComplete="off"
                      value={metaCreds.phoneNumberId}
                     
                      onChange={(e) => setMetaCreds({ ...metaCreds, phoneNumberId: e.target.value })}
                      className="bg-white/5 border-white/10 text-white focus:border-green-500/50"
                      required
                    />
                  </div>
                  <div className="space-y-2">
                    <Label className="text-gray-300">WABA ID</Label>
                    <Input
                      placeholder="WhatsApp Business Account ID"
                      value={metaCreds.wabaId}
                      autoComplete="off"
                      onChange={(e) => setMetaCreds({ ...metaCreds, wabaId: e.target.value })}
                      className="bg-white/5 border-white/10 text-white focus:border-green-500/50"
                      required
                    />
                  </div>
                  <div className="space-y-2">
                    <Label className="text-gray-300">Permanent Access Token</Label>
                    <Input
                      type="password"
                      placeholder="EAAGxxxxxxxx..."
                      autoComplete="off"
                      value={metaCreds.accessToken}
                     
                      onChange={(e) => setMetaCreds({ ...metaCreds, accessToken: e.target.value })}
                      className="bg-white/5 border-white/10 text-white focus:border-green-500/50"
                      required
                    />
                  </div>
                  <div className="space-y-2">
                    <Label className="text-gray-300">Your Phone Number (for WhatsApp OTP)</Label>
                    <Input
                      placeholder="+1 234 567 8900"
                      value={metaCreds.displayPhoneNumber}
                      autoComplete="off"
                      onChange={(e) => setMetaCreds({ ...metaCreds, displayPhoneNumber: e.target.value })}
                      className="bg-white/5 border-white/10 text-white focus:border-green-500/50"
                      required
                    />
                  </div>
                </TabsContent>

                <TabsContent value="twilio" className="space-y-4 mt-0">
                  <div className="space-y-2">
                    <Label className="text-gray-300">WhatsApp Number to Connect</Label>
                    <Input
                      placeholder="+14155238886"
                      value={twilioCreds.whatsappNumber}
                      autoComplete="off"
                      onChange={(e) => setTwilioCreds({ ...twilioCreds, whatsappNumber: e.target.value })}
                      className="bg-white/5 border-white/10 text-white focus:border-blue-500/50"
                      required
                    />
                  </div>
                  <div className="space-y-2">
                    <Label className="text-gray-300">Twilio Account SID</Label>
                    <Input
                      placeholder="ACxxxxxxxxxxxxxxxxxxxxxxxxxxxxxx"
                      value={twilioCreds.accountSid}
                      autoComplete="off"
                      onChange={(e) => setTwilioCreds({ ...twilioCreds, accountSid: e.target.value })}
                      className="bg-white/5 border-white/10 text-white focus:border-blue-500/50"
                      required
                    />
                  </div>
                  <div className="space-y-2">
                    <Label className="text-gray-300">Twilio Auth Token</Label>
                    <Input
                      type="password"
                      placeholder="Auth Token"
                      value={twilioCreds.authToken}
                      autoComplete="off"
                      onChange={(e) => setTwilioCreds({ ...twilioCreds, authToken: e.target.value })}
                      className="bg-white/5 border-white/10 text-white focus:border-blue-500/50"
                      required
                    />
                  </div>
                </TabsContent>
              </div>
            </Tabs>
          ) : (
            <div className="p-6 space-y-4">
              <div className="space-y-2">
                <Label className="text-gray-300">6-Digit Verification Code</Label>
                <Input
                  placeholder="Â· Â· Â· Â· Â· Â·"
                  value={otpCode}
                  onChange={(e) => setOtpCode(e.target.value.replace(/\D/g, '').slice(0, 6))}
                  className="bg-white/5 border-white/10 text-white text-center text-3xl tracking-[0.5em] h-16 focus:border-green-500/50"
                  required
                />
                <p className="text-xs text-gray-500 text-center">
                  Enter the code sent to {provider === 'meta' ? metaCreds.displayPhoneNumber : twilioCreds.whatsappNumber} via {provider === 'meta' ? 'WhatsApp' : 'SMS'}
                </p>
              </div>
            </div>
          )}

          <div className="px-6 pb-6">
            <div className="bg-white/5 border border-white/10 rounded-lg p-3 flex items-start gap-3 mb-4">
              <ShieldCheck className={`w-5 h-5 mt-0.5 shrink-0 ${step === 'otp' ? 'text-blue-500' : 'text-amber-500'}`} />
              <div className="text-xs">
                <p className={`font-medium ${step === 'otp' ? 'text-blue-400' : 'text-amber-400'}`}>
                  {step === 'otp' ? "Verification Step" : "Configuration Step"}
                </p>
                <p className="text-gray-500 mt-0.5">
                  {step === 'otp' 
                    ? `Enter the code received on your phone (${provider === 'meta' ? 'WhatsApp' : 'SMS'}) to verify.` 
                    : `We will send a code to your phone via ${provider === 'meta' ? 'WhatsApp' : 'SMS'} to verify ownership.`}
                </p>
              </div>
            </div>

            <DialogFooter className="gap-2 sm:gap-0">
              {step === 'otp' ? (
                <div className="flex w-full gap-2">
                  <Button
                    type="button"
                    variant="ghost"
                    onClick={() => setStep('credentials')}
                    className="text-gray-400 hover:text-white hover:bg-white/5 flex-1"
                  >
                    Back
                  </Button>
                  <Button
                    type="submit"
                    disabled={isLoading || otpCode.length !== 6}
                    className="bg-green-600 hover:bg-green-500 text-white flex-1 transition-all"
                  >
                    {isLoading ? "Connecting..." : "Confirm & Connect"}
                  </Button>
                </div>
              ) : (
                <div className="flex w-full gap-2">
                  <Button
                    type="button"
                    variant="ghost"
                    onClick={onClose}
                    className="text-gray-400 hover:text-white hover:bg-white/5 flex-1"
                  >
                    Cancel
                  </Button>
                  <Button
                    type="submit"
                    disabled={isVerifying || !isFormFilled()}
                    className="bg-indigo-600 hover:bg-indigo-500 text-white flex-1 transition-all"
                  >
                    {isVerifying ? "Sending..." : "Send Verification Code"}
                  </Button>
                </div>
              )}
            </DialogFooter>
          </div>
        </form>
      </DialogContent>
    </Dialog>
  );
};

export default WhatsappConnectModal;
