import React, { useState, useEffect, useCallback } from "react";
import { toast } from "sonner";
import axiosInstance from "@/api/axios/axiosInstance";
import { Card, CardContent } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import linkedInImg from "../../../assets/linkedin.png";

const LinkedIn = () => {
  const [isConnected, setIsConnected] = useState(false);
  const [profile, setProfile] = useState(null);
  const [isLoading, setIsLoading] = useState(true);

  const checkConnection = async () => {
    try {
      setIsLoading(true);
      const response = await axiosInstance.get("/api/linkedin/auth/status");
      if (response.data.success && response.data.data.isConnected) {
        setIsConnected(true);
        setProfile(response.data.data.profile);
      } else {
        setIsConnected(false);
        setProfile(null);
      }
    } catch (error) {
      console.error("Error checking LinkedIn connection:", error);
    } finally {
      setIsLoading(false);
    }
  };

  useEffect(() => {
    checkConnection();
  }, []);

  const handleConnect = async () => {
    try {
      const response = await axiosInstance.get("/api/linkedin/auth/url");
      if (response.data.success && response.data.data.authUrl) {
        window.open(response.data.data.authUrl, "linkedin-auth", "width=600,height=600");
      }
    } catch (error) {
      console.error("Error initiating LinkedIn OAuth:", error);
      toast.error("Failed to start LinkedIn authentication");
    }
  };

  const handleDisconnect = async () => {
    try {
      const response = await axiosInstance.delete("/api/linkedin/auth/disconnect");
      if (response.data.success) {
        setIsConnected(false);
        setProfile(null);
        toast.success("LinkedIn disconnected successfully");
      }
    } catch (error) {
      console.error("Error disconnecting LinkedIn:", error);
      toast.error("Failed to disconnect LinkedIn");
    }
  };

  const handleOAuthResult = useCallback((data) => {
    if (data.type === "LINKEDIN_AUTH_SUCCESS") {
      setIsConnected(true);
      if (data.data) {
        setProfile({ email: data.data.email });
      }
      toast.success("LinkedIn connected successfully!");
      checkConnection();
    } else if (data.type === "LINKEDIN_AUTH_ERROR") {
      toast.error(data.message || "Failed to connect LinkedIn");
    }
  }, []);

  useEffect(() => {
    const handleMessage = (event) => {
      if (event.data?.type === "LINKEDIN_AUTH_SUCCESS" || event.data?.type === "LINKEDIN_AUTH_ERROR") {
        handleOAuthResult(event.data);
      }
    };

    let linkedinChannel = null;
    try {
      linkedinChannel = new BroadcastChannel("linkedin_oauth_channel");
      linkedinChannel.onmessage = (event) => {
        if (event.data) handleOAuthResult(event.data);
      };
    } catch (e) {}

    const checkLocalStorage = () => {
      try {
        const result = localStorage.getItem("linkedin_oauth_result");
        if (result) {
          const data = JSON.parse(result);
          if (data.timestamp && Date.now() - data.timestamp < 30000) {
            localStorage.removeItem("linkedin_oauth_result");
            handleOAuthResult(data);
          }
        }
      } catch (e) {}
    };

    const interval = setInterval(checkLocalStorage, 500);
    window.addEventListener("message", handleMessage);

    return () => {
      window.removeEventListener("message", handleMessage);
      if (linkedinChannel) linkedinChannel.close();
      clearInterval(interval);
    };
  }, [handleOAuthResult]);

  return (
    <Card className="w-full max-w-sm bg-white/5 backdrop-blur-md border border-white/10 rounded-lg shadow-sm hover:shadow-md hover:border-indigo-500/30 transition-all duration-200">
      <CardContent className="flex flex-col items-center text-center p-2 space-y-6">
        <div className="w-20 h-20 rounded-full overflow-hidden flex items-center justify-center bg-white/10 border-2 border-white/20">
          <img src={linkedInImg} alt="LinkedIn" className="w-full h-full object-cover" />
        </div>
        <p className="text-gray-300 text-sm leading-relaxed">
          {isConnected && profile?.email ? `Connected: ${profile.email}` : "Connect your location's LinkedIn Account"}
        </p>
        <Button
          onClick={isConnected ? handleDisconnect : handleConnect}
          className={`w-full py-3 rounded-md font-medium transition-colors duration-200 ${
            isConnected
              ? "bg-gradient-to-r from-red-500 via-red-500 to-red-500 text-white border-0"
              : "bg-gradient-to-r from-indigo-600 to-purple-600 hover:from-indigo-500 hover:to-purple-500 text-white border-0"
          }`}
        >
          {isConnected ? "Disconnect" : "Connect"}
        </Button>
      </CardContent>
    </Card>
  );
};

export default LinkedIn;
